import { useState, useEffect } from "react";
import { useRecoilState, useRecoilValue } from "recoil";
import {
  toDoSelector,
  categoryState,
  defaultCategories,
  dynamicCategoriesState,
} from "../atoms";
import AddCategory from "./AddCategory";
import CreateTodo from "./CreateTodo";
import ToDo from "./ToDo";

function ToDoList() {
  const toDos = useRecoilValue(toDoSelector);
  const [category, setCategory] = useRecoilState(categoryState);
  useEffect(() => {
    //      category  .
    const savedCategory = localStorage.getItem("category");
    if (savedCategory) {
      setCategory(JSON.parse(savedCategory));
    }
  }, []); //      effect    .

  const onInput = (event: React.FormEvent<HTMLSelectElement>) => {
    const newCategory = event.currentTarget.value as any;
    setCategory(newCategory);

    // category      .
    localStorage.setItem("category", JSON.stringify(newCategory));
  };
  const dynamicCategories = useRecoilValue(dynamicCategoriesState);

  const allCategories = [...defaultCategories, ...dynamicCategories];

  return (
    <div>
      <h1>To Dos</h1>
      <br />
      <AddCategory />
      <hr />
      <select value={category} onInput={onInput}>
        {allCategories
          .filter((cat) => cat !== "DELETE")
          .map((cat, index) => (
            <option key={index} value={cat}>
              {cat}
            </option>
          ))}
      </select>
      <CreateTodo />
      {toDos?.map((toDo) => (
        <ToDo key={toDo.id} {...toDo} />
      ))}
    </div>
  );
}

export default ToDoList;
